<template>
    <div id="work">
      <div class="boundary">
        <h1>{{title}}</h1>
        <h3>{{title_children}}</h3>
        <div class="work-list" v-for="work in workList">
          <div class="time-line">
            <i class="dot-icon"></i>
            <span>{{work.beginTime}} --- {{work.endTime}}</span>
          </div>
          <div class="work-details">
            <el-row >
              <el-col :span="4">
                <img :src="work.imageurl" alt="">
              </el-col>
              <el-col :span="20">
                <p class="title1">{{work.corporateName}} --- <span  class="title2" >{{work.title}}</span></p>
                <p  class="content"  v-html="work.content"></p>
              </el-col>
            </el-row>
          </div>



        </div>

      </div>
      <!--<div class="line"  id="line"></div>-->
    </div>
</template>

<script>
    export default {
        name: "work",
        data(){
          return {
            title:"",
            title_children:"",
            workList: "",
          }
        },
      methods: {

      },
      mounted:function(){
        let _that = this;
        this.title = this.$resumeData.work.title;
        this.title_children = this.$resumeData.work.title_children;
        this.workList = this.$resumeData.work.workList;


       /* this.$nextTick(function(){

          var workDomObjs=document.getElementsByClassName("work-list");

          let workHeight = 0;
          for (let i=0; i < workDomObjs.length; i++){
            console.log(workDomObjs[i].offsetHeight);
            if(i = workDomObjs.length-1){
              break;
            }
            workHeight += workDomObjs[i].offsetHeight;
          }
          console.log(document.getElementById("line"))
          document.getElementById("line").style.height = workHeight + "px";
         // document.getElementById("line").offsetHeight = workHeight;



        })*/
      }
    }
</script>

<style scoped>
  #work{
    position: relative;
  }
  #work .work-list{
    position: relative;
    z-index: 2;
    border-bottom: 1px dashed #ccc;
    padding-bottom: 16px;
  }

/*
  #work .line{
    position: absolute;
    width: 4px;
    height: 100px;
    background-color: #ccc;
    left: -17px;
    top: 73px;
    z-index: 1;
  }*/
  #work h1{
    font-size: 25px;
    letter-spacing: 2px;
    text-align: center;
  }
  #work h3{
    font-size: 20px;
    color: #999;
    margin-top: 10px;
    letter-spacing: 2px;
    margin-bottom: 20px;
    text-align: center;
  }
  .work-list .time-line{
    position: relative;
    margin-top: 28px;
  }
  .work-list .time-line .dot-icon{
    display: inline-block;
    width: 18px;
    height: 18px;
    border: 3px solid #fff;
    border-radius: 12px;
    background: #3498db;
    position: absolute;
    left: -28px;
    top: -4px;
  }
  .work-list .work-details{
    margin-top: 20px;
  }
  .work-list .work-details .content{
    line-height: 26px;
    margin-top: 10px;
  }
  .work-list .work-details .title1{
    font-size: 20px;
  }
  .work-list .work-details .title2{
    font-size: 18px;
    color: #888;
  }
  .work-details img{
    width: 100px;
    margin: auto;
    display: block;
  }



</style>



