<template>
    <div id="project-list">
      <div class="boundary">
        <h1>{{title}}</h1>
        <h3>{{title_children}}</h3>
        <div class="projects">
          <div class="project" v-for = "project in projectList">
            <p class="p1">{{project.name}}</p>
            <p class="p2">
              <v_skillicon :iconname="icon" v-for="icon in project.skillIcon"  class="icon"></v_skillicon>
              <span class="time" style="">{{project.beginTime}}---{{project.endTime}}</span>
            </p>
            <p class="p3" v-html="project.content">
            </p>
           <!-- <p class="p4">

            </p>-->
          </div>
        </div>
      </div>
    </div>
</template>

<script>
  import skillicon from "../public/skillIcon.vue";
    export default {
        name: "project-list",
        data(){
          return{
            title:"",
            title_children:"",
            projectList:""

          }
        },
        methods:{

        },
        mounted:function(){
          this.title = this.$resumeData.project.title;
          this.title_children = this.$resumeData.project.title_children;
          this.projectList = this.$resumeData.project.projectList;
          console.log(this.$resumeData)
        },
        components:{
          "v_skillicon": skillicon
        }
    }

</script>

<style scoped>
  #project-list h1{
    font-size: 25px;
    letter-spacing: 2px;
    text-align: center;

  }
  #project-list h3{
    font-size: 20px;
    color: #999;
    margin-top: 10px;
    letter-spacing: 2px;
    margin-bottom: 20px;
    text-align: center;
  }
  .project{
    margin-bottom: 25px;
    padding-bottom: 20px;
    border-bottom: 1px solid #ccc;
  }
  .project span{
    display: inline-block;
    float: right;
    line-height: 18px;
    font-size: 14px;
  }
  .project .p1{
    font-size: 20px;
  }
  .project .p2{
    margin-top: 16px;
  }
  .project .p3{
    margin-top: 16px;
    line-height: 24px;
    display: inline-block;
    font-size: 14px;
    /*width: 69%;*/
  }
  /*.project .p4{
    display: inline-block;
    width: 30%;

  }*/
  .icon{
    margin-left: 4px;
  }
</style>



