<template>
  <div id="pc-header">
    <div class="boundary">
      <div class="left">
        <span class="name">{{ name }}</span>
        <span class="title">{{ title }}</span>
      </div>
      <div class="right">
        <ul>
          <li>
            <img src="../../assets/images/qq_icon.png" >
            <span>{{qq}}</span>
          </li>
          <li>
            <img src="../../assets/images/mail.png" >
            <span>{{mail}}</span>
          </li>
          <li v-if="gitHubUrl">
            <img src="../../assets/images/git.png">
            <a :href="gitHubUrl">GitHub</a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "pc-header",
    data(){
      return{
        name: "",
        title: "",
        qq:"",
        mail:"",
        gitHubUrl:""
      }
    },
    mounted:function(){
      this.name = this.$resumeData.name;
      this.title = this.$resumeData.title;
      this.qq = this.$resumeData.qq;
      this.mail = this.$resumeData.mail;
      this.gitHubUrl = this.$resumeData.gitHubUrl;
    }
  }
</script>

<style scoped>
  #pc-header{
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    border-bottom: 1px solid #d6d6d6;
    padding: 15px 0;
    z-index: 9999;
    background-color: #fff;

  }
  #pc-header .left{
    float: left;
  }
  #pc-header .left .name{
    font-size: 30px;
    letter-spacing: 2px;
  }
  #pc-header .left .title{
    font-size: 15px;
    color: #888;
  }
  #pc-header .right{
    float: right;
    text-align: right;
  }
  #pc-header .right ul{
    margin-top: 14px;
  }
  #pc-header .right ul li{
    float: left;
    font-size: 10px;
    line-height: 20px;
    margin-left: 8px;
  }

  #pc-header .right ul img{
    width: 14px;
  }

  #pc-header .right:after{
    clear: both;
  }


</style>



