import Vue from 'vue'
import Router from 'vue-router'
import PCMain from '@/components/pc/pcMain';
import MMain from '@/components/mobile/mMain';

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/pcmain',
      name: 'PCMain',
      component: PCMain
    },
    {
      path: '/mmain',
      name: 'MMain',
      component: MMain
    }
  ]
})



