<template>
  <div id="synopsis">

    <div class="boundary">
      <h1>{{title}}</h1>
      <h3>{{title_children}}</h3>

      <div class="progress" v-for="(skill,index) in skills" @mouseenter="progressMouseOn(index)" @mouseleave="progressMouseLeave">
        <span>{{skill.name}}</span>
        <div class="tip" v-if="index == nowprogress">
          <ul>
            <li v-for="s in skill.synopsis">{{s}}</li>
          </ul>
        </div>
        <el-progress  :stroke-width="30" :percentage="skill.proficiency" :show-text="false" :status="skill.proficiency | setStatus(skill.proficiency)"></el-progress>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "synopsis",
        data(){
          return{
            title: "",
            title_children: "",
            skills:"",
            nowprogress: -1
          }
        },
      methods:{
          progressMouseOn: function(val){
           this.nowprogress=val;
          },
        progressMouseLeave: function(){
          this.nowprogress=-1;
        }
      },
     filters:{
          setStatus: function(val){
            if(val >= 80){
              return "success";
            }else if(val >= 50){
              return ""
            }else {
              return "exception"
            }
          }
     },
      mounted:function(){
        this.title = this.$resumeData.synopsis.title;
        this.title_children = this.$resumeData.synopsis.title_children;
        this.skills = this.$resumeData.skills;
        console.log(this.$resumeData)
      }
    }
</script>

<style scoped>
  #synopsis{
    text-align: center;

  }
  #synopsis h1{
    font-size: 25px;
    letter-spacing: 2px;

  }
  #synopsis h3{
    font-size: 20px;
    color: #999;
    margin-top: 10px;
    letter-spacing: 2px;
    margin-bottom: 20px;


  }
  #synopsis .progress{
    position: relative;
    margin-top: 10px;
  }
  #synopsis .progress .tip{
    position:absolute;
    width: 280px;
    background-color: rgba(255, 255, 255, 0.9);
    left: 200px;
    top: 8px;
    z-index: 9997;
    text-align: left;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.2);
    border-radius: 4px;;
  }
  #synopsis .progress .tip ul{
    padding: 4px;
    padding-left: 10px;
    font-size: 12px;
    line-height: 18px;
    margin-top: 2px;
    margin-bottom: 2px;
  }
  #synopsis .progress .tip ul li{
    line-height: 25px;

  }
  #synopsis .progress .tip ul li:nth-child(odd){
    color: #409EFF;
  }
  #synopsis .progress .tip ul li:nth-child(even){
    color: #f56c6c;
  }





  #synopsis .progress span{
    position:absolute;
    left: 15px;
    top: 6px;
    z-index: 9998;
    text-align: left;

  }


</style>



