<template>
  <div id="pc-main">
    <v_pc_header></v_pc_header>
    <v_pc_synopsis class="structure"></v_pc_synopsis>
    <v_pc_work class="back structure"></v_pc_work>
    <v_pc_project class="structure"></v_pc_project>

  </div>
</template>

<script>
  import pc_header from "./pc-header.vue";
  import pc_synopsis from "./synopsis.vue";
  import pc_work from "./work.vue";
  import pc_project from "./projectList.vue";
    export default {
      name:"pc-main",
      components:{
        "v_pc_header": pc_header,
        "v_pc_synopsis": pc_synopsis,
        "v_pc_work": pc_work,
        "v_pc_project": pc_project
      }
    }
</script>

<style scoped>
  #pc-main{
    padding-top: 100px;
  }
  .structure{
    margin-bottom: 20px;
    padding-top: 40px;
    padding-bottom: 20px;
  }
  .back{
    background: #fafafa;
  }



</style>



