<template>
    <div id="skill-icon" :style="{color:color,background:background}">
      {{name}}
    </div>
</template>

<script>
    export default {
        name: "skill-icon",
        data(){
          return{
            name: "",
            color:"",
            background:"",
            configure:"",
          }
        },
      props: ['iconname'],
      mounted:function(){
          this.configure=this.$resumeData.iconconfigure;
          this.name=this.configure[this.iconname].name;
          this.color=this.configure[this.iconname].color;
          this.background=this.configure[this.iconname].background;
      }
    }
</script>

<style scoped>
  #skill-icon{
    display: inline-block;
    height: 18px;
    border-radius: 3px;
    line-height: 18px;
    font-size: 12px;
    padding-left: 4px;
    padding-right: 4px;
  }

</style>



